@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: medium;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  font-family: "Arial", "sans-serif";
  font-size: 14px;
  line-height: 18px;
}
.page__body {
  min-height: 100%;
  min-width: 280px;
  margin: 0;
  background-color: #FFFFFF;
  color: #444444;
}
.button {
  display: block;
  padding: 11px;
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  background: #68B738;
  border: none;
}
.button:hover {
  background: #5EAA2F;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background: #5EAA2F;
}
.page-header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}
@media (max-width: 767px) {
  .page-header {
    padding-top: 10px;
    flex-direction: column;
  }
}
.page-header__wrapper {
  display: flex;
  justify-content: center;
  height: 100%;
}
@media (max-width: 767px) {
  .page-header__wrapper {
    padding-bottom: 14px;
  }
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}
.page-header__logo-1 {
  display: block;
}
@media (min-width: 768px) {
  .page-header__logo-1 {
    display: none;
  }
}
.page-header__logo-2 {
  display: none;
}
@media (min-width: 768px) {
  .page-header__logo-2 {
    display: block;
  }
}
.page-header__logo-2--desktop {
  display: none;
}
@media (min-width: 1200px) {
  .page-header__logo-2--desktop {
    display: block;
  }
}
.page-header__toggle {
  margin: 0;
  padding: 0;
  border: none;
  margin-left: 66px;
  width: 24px;
  background: rgba(0, 0, 0, 0);
}
.page-header__toggle--closed span {
  display: block;
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px #000000, 0 -8px #000000;
}
.page-header__toggle--opened span::before,
.page-header__toggle--opened span::after {
  content: '';
  display: block;
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.page-header__toggle--opened span::before {
  transform: rotate(45deg);
}
.page-header__toggle--opened span::after {
  transform: rotate(-45deg);
}
.page-header__list {
  padding: 0px;
  margin: 0px;
}
.site-list {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: "Oswald", "sans-serif";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  list-style: none;
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}
.site-list__item {
  display: flex;
  justify-content: center;
  height: 100%;
}
@media (max-width: 767px) {
  .site-list__item {
    padding: 22px;
    border-top: 1px solid #CDCDCD;
  }
  .site-list__item:last-child {
    border-bottom: 1px solid #CDCDCD;
  }
}
.site-list__item a {
  text-decoration: none;
  color: #000000;
}
.site-list__item a:hover {
  opacity: 0.6;
}
.site-list__item a:active {
  opacity: 0.3;
}
.page-main {
  margin: 0px;
}
.promo__background {
  position: absolute;
  z-index: -1;
  height: 305px;
  width: 100%;
  background: #65A93C url(../img/index-background-mobile@1x.jpg) no-repeat center;
}
@media (min-resolution: 2dppx) {
  .promo__background {
    background: #65A93C url(../img/index-background-mobile@2x.jpg) no-repeat center;
    background-size: auto 100%;
  }
}
@media (min-width: 1200px) {
  .promo__background {
    background: url(../img/index-background-desktop@1x.jpg) no-repeat right;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .promo__background {
    background: url(../img/index-background-desktop@2x.jpg) no-repeat right;
    background-size: auto 100%;
  }
}
.promo__wrapper {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 20px;
}
.promo__title {
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  color: #FFFFFF;
}
.promo__title--small {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
}
.promo__picture {
  margin: auto;
  width: 100%;
  height: 100%;
  padding-bottom: 3px;
}
.capabilitys__item {
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
  padding: 20px;
  background-color: #F2F2F2;
}
.capabilitys__title {
  margin: 7px;
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
}
.capabilitys__description {
  width: 100%;
  margin-top: 20px;
  padding-bottom: 23px;
  font-family: 'Arial';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  border-bottom: 1px solid #CDCDCD;
}
.capabilitys__button {
  margin-top: 7px;
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}
.capabilitys__picture-conteiner {
  order: -1;
  margin-left: -8px;
}
.capabilitys__picture-conteiner::before {
  position: absolute;
  display: block;
  width: 50px;
  height: 50px;
  margin-top: 3px;
  margin-left: 10px;
  border-radius: 60px;
  background: #5EAA2F;
  content: "";
}
.capabilitys__picture {
  position: relative;
  width: 70px;
  height: 50px;
}
.advantages {
  display: flex;
  flex-direction: column;
  margin: 20px;
  padding-bottom: 10px;
}
.advantages__header {
  margin-top: 5px;
  margin-bottom: 30px;
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  text-align: left;
}
.advantages__item {
  display: flex;
  margin: 10px 0;
}
.advantages__picture {
  object-fit: cover;
  height: 100%;
  padding: 5px;
  background: #5EAA2F;
}
.advantages__description {
  margin: 0;
  margin-left: 20px;
}
.example {
  padding: 20px;
  background: #F2F2F2;
}
.example__title {
  margin-top: 5px;
  margin-bottom: 30px;
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  text-align: left;
}
.example__descriotion {
  font-family: 'Arial';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}
.example__commentar {
  display: block;
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}
.stats {
  display: flex;
  justify-content: space-between;
}
.stats__item {
  margin-top: 15px;
  padding-bottom: 20px;
}
.stats__term {
  position: absolute;
  max-width: 80px;
  margin-top: 47px;
  margin-left: 22px;
  font-family: 'Arial';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #444444;
  background: #F2F2F2;
}
.stats__description {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  min-width: 122px;
  min-height: 54px;
  font-family: 'Oswald';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  color: #000000;
  border: solid 1px #CDCDCD;
  border-radius: 2px;
}
.slider {
  margin-top: 35px;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  margin: auto;
}
.slider__container {
  align-self: center;
  width: 311px;
  height: 232px;
  background: url(../img/before-mobile@1x.png) no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .slider__container {
    background: url(../img/before-mobile@2x.png) no-repeat;
    background-size: cover;
  }
}
@media (min-width: 768px) {
  .slider__container {
    background: url(../img/before-tablet@1x.png) no-repeat;
    background-size: cover;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .slider__container {
    background: url(../img/before-tablet@2x.png) no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1200px) {
  .slider__container {
    background: url(../img/before-desktop@1x.png) no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .slider__container {
    background: url(../img/before-desktop@1x.png) no-repeat;
    background-size: cover;
  }
}
.slider__container div {
  box-sizing: border-box;
  height: 100%;
  width: 10%;
  max-width: 99%;
  resize: horizontal;
  overflow: auto;
  position: relative;
  background: #F2F2F2 url(../img/after-mobile@1x.png) no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .slider__container div {
    background: #F2F2F2 url(../img/after-mobile@2x.png) no-repeat;
    background-size: cover;
  }
}
@media (min-width: 768px) {
  .slider__container div {
    background: #F2F2F2 url(../img/after-tablet@1x.png) no-repeat;
    background-size: cover;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .slider__container div {
    background: #F2F2F2 url(../img/after-tablet@2x.png) no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1200px) {
  .slider__container div {
    background: #F2F2F2 url(../img/after-desktop@1x.png) no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .slider__container div {
    background: #F2F2F2 url(../img/after-desktop@1x.png) no-repeat;
    background-size: cover;
  }
}
.slider__control-bar {
  align-self: center;
}
